$color-white: #ffffff;
$color-black: #121212;

$color-red: #f44336;
$color-blue: #1a73e8;
$color-gray: #80868b;

$shadow-medium: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
0 2px 4px -1px rgba(0, 0, 0, 0.06);
$shadow-large: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
0 4px 6px -2px rgba(0, 0, 0, 0.05);

// Components
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 80rem;
    width: 100%;
    padding: 0 2rem;
    margin: auto;

    .text {

        &-large {
            font-size: 2rem;
            font-weight: 600;
            color: $color-black;
        }

        &-normal {
            font-size: 1rem;
            font-weight: 400;
            color: $color-black;
        }

        &-links {
            font-size: 1rem;
            font-weight: 400;
            color: $color-blue;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    .wrapper {
        max-width: 35rem;
        width: 100%;
        margin: 2rem auto;
        padding: 3rem 2.5rem;
        border: none;
        outline: none;
        border-radius: 1.25rem;
        color: $color-black;
        background: $color-white;
        box-shadow: $shadow-large;

        .form {
            width: 100%;
            height: auto;
            margin-top: 2rem;

            .input-control {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 1.25rem;
            }

            .input-field {
                font-family: inherit;
                font-size: 20px;
                width: 100%;
                height: auto;
                padding: 0.75rem 1.25rem;
                border: none;
                outline: none;
                border-radius: 2rem;
                color: $color-black;
                background: #f1f5f9;
            }

            .input-submit {
                font-family: inherit;
                font-size: 20px;
                cursor: pointer;
                min-width: 30%;
                height: auto;
                padding: 0.65rem 1.25rem;
                border: none;
                outline: none;
                border-radius: 2rem;
                color: $color-white;
                background: $color-blue;
                box-shadow: $shadow-medium;
            }
        }
    }
}