$grey-100: #f4f6f8;
$grey-200: #e3e3e3;
$grey-300: #b2b2b2;
$grey-400: #7b7b7b;
$grey-500: #3d3d3d;
$blue-500: #688afd;

.timeline {
    max-width: 700px;
	margin: 0 auto;
    display: flex;
    flex-direction: column;
    padding: 32px 0 32px 32px;
    border-left: 2px solid $grey-200;
	font-family: inherit;

	&__timeline-item {
		display: flex;
		gap: 24px;
	
		&+* {
			margin-top: 24px;
		}
	
		&+.extra-space {
			margin-top: 48px;
		}
	}
	
	&__new-thread {
		width: 100%;

		.thread-form {
			display: flex;
			gap: 20px;

			.thread-button {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 45px;
				height: 45px;
				border-radius: 50%;
				margin-left: -40px;
				flex-shrink: 0;
				overflow: hidden;
				box-shadow: 0 0 0 6px #eee;
				background-color: $blue-500;
				color: #fff;
				border: none;
				cursor: pointer;
			}

			input {
				border: 1px solid $grey-200;
				border-radius: 8px;
				height: 50px;
				padding: 0 16px;
				width: 100%;
				font-family: inherit;
				font-size: large;
		
				&::placeholder {
					color: $grey-300;
				}
		
				&:focus {
					border-color: $grey-300;
					outline: 0;
					box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
				}
			}
		}
	
	}

	&__timeline-item-icon {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 45px;
		height: 45px;
		border-radius: 50%;
		margin-left: -55px;
		flex-shrink: 0;
		overflow: hidden;
		box-shadow: 0 0 0 6px #eee;
	
		svg {
			width: 20px;
			height: 20px;
		}

		&.filled-icon {
			background-color: $blue-500;
			color: #fff;
		}

		&.filled-icon-white {
			background-color: #fff;
		}
	}
	
	&__timeline-item-description {
		display: flex;
		padding-top: 6px;
		gap: 8px;
		color: $grey-400;
	
		img {
			flex-shrink: 0;
		}
	
		a, p {
			color: $grey-500;
			font-weight: 500;
			text-decoration: none;
			display: inline;
	
			&:hover,
			&:focus {
				outline: 0;
				color: $blue-500;
			}
		}
	}

	&__thread {
		margin-top: 12px;
		background-color: #fff;
		color: $grey-500;
		border: 1px solid $grey-200;
		box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
		border-radius: 8px;
		padding: 16px;
		font-size: 18px
	}	
}
